.more_div{
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 5px solid #787575;
margin-top: 50px;

}
.more_div img{
  height: 130px;
}
.uni_img img{
  width: 100%;
}
.yec_nav{
  background: rgba(255, 255, 255, 0.5);
  display: flex;
  justify-content: space-between;
  height: 50px;
  width: 100vw;
  position: fixed;
  top: 0px;

}
.more_head{
  text-align: right;
  margin-right: 2vw;
}
.more_details{
  margin-top: 50px;
  text-align: center;

}
.course_details{
  padding: 10px;
  background: #DAD8D8;
  margin: 20px 15vw ;
  border-radius: 20px;
  position: relative;
  bottom: 60px;
}
.course_details_element{
  margin-right: 10px;
  text-align: center;
}
.CourseDetailsApply{
  margin-bottom: 20px;
  height: 5vh;
    width: 10vw;
    border-radius: 5px;
    border: none;
    background: red;
    color: white;
}
.CourseDetailsApply:hover{
  cursor: pointer;
  background: #1a7cbd;
}