.CourseShort{
    border: 1px solid #818181;
    border-radius: 10px;
    margin: 10px;
    padding: 10px;
    background: white;
}
.course_top{
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.course_details_row{
    display: flex;
    justify-content: space-between;
}
.course_details_statment{
    display: flex;
    align-items: center;
}
.course_details_statment span{
    text-transform: capitalize;
}
.course_details_statment img{
width: 20px;
height: 20px;
margin: 2px;
}

.course_buttons{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.course_buttons button{
    height:45%;
    width: 130%;
    background: red;
    border: none;
    color: white;
    position: relative;
    right: 45%;
    border-radius: 2px;
}
.course_buttons button:hover{
    cursor: pointer;
    background: #1a7cbd;
}
.more_button{
    height:45%;
    width: 130%;
    background: #2E86C1;
    border: none;
    color: white;
    position: relative;
    right: 45%;
    border-radius: 2px;
    text-decoration: none;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    transition:300ms cubic-bezier(0.075, 0.82, 0.165, 1);
}
.more_button:hover{
    cursor: pointer;
    background: red;

}
.Uni_Logo{
    width: max-content;
    height: 50px;
}