.SearchCources{
    margin-top: 100px;
   
   
}
.idk{
    height: 86vh;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.SearchBoxDiv{
    text-align: center;
}
.SearchBox{
    display: flex;
    align-items: center;
    justify-content: center;
    height: 50px;
}
.SearchBox input{
    width: 500px;
   height: 50px;
}
.SearchBox button{
   height: 50px;
width: 200px;
    margin-left: 30px;
    background-color: #2E86C1;
    border: none;
    color: white;
}
.SearchBox button:hover{
    cursor: pointer;
    opacity: 0.8;
    background: red;
}
.SearchBody{
    display: flex;
border: 1px solid #E4E4E4;

}
.SearchResults{
   background:  #E4E4E4;
width: 80vw;
height: 76vh;
overflow-y: scroll;

}

.AdvanceFilters{
    padding: 0px 1vw;
    width: 20vw;
    /* height:70vh; */
   /* background: grey; */
   display: flex;
    flex-direction: column;
    /* justify-content: space-between; */
    align-items: stretch;
}
.AdvanceFilters h4{
    text-align: center;
    padding-bottom: 5px;
    border-bottom: 1px solid #E4E4E4;
}
._2zTZb{
    /* margin-top: 10px;  */
    width: 100% !important;
}
._2yQu9 {
width: 25px !important;
height: 25px !important;
}
.AdvanceFilters .FeeFilter{
    margin-top: 10px;
    height: 70px;
}
.AdvanceFilters .Ammount{
    display: flex;
    justify-content: center;
    align-items: center;
}
.AdvanceFilters .Ammount input{
width: 100px;
height: 40px;
}
.Apply_Filters{
    margin: 0px 3vw;
    height: 40px;
